<template>
<div id="menu">
<h1>Apocalypse Manager</h1>

<div id="loading" v-if="loading">
    <div class="bar">
        <div id="loadingProgress" class="barcompleted"></div>
        <p>Loading v{{version}}...</p>
    </div>
</div>

<ul v-else>
    <li class="action">
        <a @click="newGame()">Nouvelle partie</a>
        </li>
    <li class="action" v-if="hasSavedGame">
        <a @click="continueGame()">Continuer</a>
    </li>
    <li class="action">
        <router-link to="/credits">Crdits</router-link>
    </li>
</ul>

<footer>
    v{{version}}
</footer>
</div>
</template>

<script>
import { version, newGame, loadSavedGame, hasSavedGame } from "@/game";
import { preloadGame } from "@/preloader";
import { playMusic, SOUNDS } from "@/audio";

export default {
  name: "Menu",

  data() {
    return {
      version: version,
      loading: true,
      hasSavedGame: hasSavedGame()
    };
  },

  mounted() {
    document.getElementById("app").style.backgroundSize = "1px";
    document.getElementById("loadingProgress").style.width = "0%";

    preloadGame(this.onProgress).then(({ sounds }) => {
      Object.assign(SOUNDS, sounds);
      this.loading = false;
      playMusic("TitleScreen");
    });
  },

  methods: {
    onProgress(percentage) {
      let meteorSize = Math.pow(percentage / 100, 2) * 300;
      document.getElementById("app").style.backgroundSize = meteorSize + "px";
      document.getElementById("loadingProgress").style.width = percentage + "%";
    },

    newGame() {
      newGame();
      this.$router.push("/messages");
    },

    continueGame() {
      loadSavedGame();
      this.$router.push("/game/");
    }
  }
};
</script>


