<template>
    <div class="countdown">
        {{formattedCountdown}}
    </div>
</template>

<script>
import { dureeJeu, state, eatAndDrink, nextEpoch } from "@/game";
import { formatTime } from "@/labels";

export default {
  data() {
    return {
      timeLeft: state.apocalypseDate - Date.now()
    };
  },

  created() {
    let counterInterval = 100;
    this.timeInterval = setInterval(() => {
      this.timeLeft = state.apocalypseDate - Date.now();
      let meteorSize =
        Math.pow((dureeJeu - this.timeLeft) / dureeJeu, 2) * 1000;
      document.getElementById("app").style.backgroundSize = meteorSize + "px";

      if (this.timeLeft < 0) {
        this.$router.push("/gameover");
        return;
      }

      let timeBetweenEpochs = 1000 * 60 * 6; // 6 minutes

      if (
        this.timeLeft < timeBetweenEpochs * (5 - state.currentEpoch) &&
        state.currentEpoch < 5
      ) {
        nextEpoch();
        this.$router.push("/messages");
      }

      let realTimeForADay = 60 * 1000;

      if (this.timeLeft % realTimeForADay < counterInterval) {
        eatAndDrink(emptyRessource => {
          this.$router.push("/gameover/" + emptyRessource);
        });
      }
    }, counterInterval);
  },

  destroyed() {
    clearInterval(this.timeInterval);
  },

  computed: {
    formattedCountdown() {
      let timeLeft = this.timeLeft;
      timeLeft *= state.speed;
      return formatTime(timeLeft, true);
    }
  }
};
</script>

<style>
.countdown {
  font-size: 1.5em;
  color: red;
  border: 4px solid red;
}
</style>
