//import { Dom } from "@/utils"

export const SOUNDS = {}

let music;

export const stopMusic = () => {
    if (music) {
        music.pause();
        music.currentTime = 0;
    }
}

export const playMusic = function (name, loop = true) {
    if (!SOUNDS[name]) return console.error("musique non trouve ?", name)
    stopMusic();
    music = SOUNDS[name];
    music.volume = 0.7;
    music.loop = loop;
    //music.muted = (Dom.storage.muted === "true");
    try { music.play(); } catch (e) { console.error(e) }
    /*Dom.toggleClassName('mute', 'on', music.muted);
    Dom.on('mute', 'click', function () {
        Dom.storage.muted = music.muted = !music.muted;
        Dom.toggleClassName('mute', 'on', music.muted);
    });*/
}

export const playSound = function (name) {
    //if (Dom.storage.muted !== "true") {
    try { SOUNDS[name].cloneNode(true).play(); } catch (e) { console.error(e) }
    //}
}