export const coinflip = (n = 2) => {
    return Math.random() <= 1 / n
}

export const randomBetween = (a, b) => {
    return Math.floor(a + Math.random() * (b - a + 1))
}

export const pickRandom = list => {
    return list[Math.floor(Math.random() * list.length)];
}

export const clone = o => JSON.parse(JSON.stringify(o));

export const Dom = {

    get: function (id) { return ((id instanceof HTMLElement) || (id === document)) ? id : document.getElementById(id); },
    set: function (id, html) { Dom.get(id).innerHTML = html; },
    on: function (ele, type, fn, capture) { Dom.get(ele).addEventListener(type, fn, capture); },
    once: (ele, type, fn, capture) => {
        let oneTimeCall = (e) => {
            fn(e);
            Dom.un(ele, type, oneTimeCall, capture)
        }
        Dom.on(ele, type, oneTimeCall, capture)
    },
    un: function (ele, type, fn, capture) { Dom.get(ele).removeEventListener(type, fn, capture); },
    show: function (ele, type) { Dom.get(ele).style.display = (type || 'block'); },
    hide: function (ele) { Dom.get(ele).style.display = 'none'; },
    blur: function (ev) { ev.target.blur(); },

    addClassName: function (ele, name) { Dom.toggleClassName(ele, name, true); },
    removeClassName: function (ele, name) { Dom.toggleClassName(ele, name, false); },
    toggleClassName: function (ele, name, on) {
        ele = Dom.get(ele);
        var classes = ele.className.split(' ');
        var n = classes.indexOf(name);
        on = (typeof on == 'undefined') ? (n < 0) : on;
        if (on && (n < 0))
            classes.push(name);
        else if (!on && (n >= 0))
            classes.splice(n, 1);
        ele.className = classes.join(' ');
    },

    storage: window.localStorage || {},

    toggleFullScreen: function () {
        if (!document.fullscreenElement &&    // alternative standard method
            !document.mozFullScreenElement && !document.webkitFullscreenElement) {  // current working methods
            Dom.toggleClassName("fullscreen", "on", true);
            if (document.documentElement.requestFullscreen) {
                document.documentElement.requestFullscreen();
            } else if (document.documentElement.mozRequestFullScreen) {
                document.documentElement.mozRequestFullScreen();
            } else if (document.documentElement.webkitRequestFullscreen) {
                document.documentElement.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
            }
        } else {
            Dom.toggleClassName("fullscreen", "on", false);
            if (document.cancelFullScreen) {
                document.cancelFullScreen();
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen();
            } else if (document.webkitCancelFullScreen) {
                document.webkitCancelFullScreen();
            }
        }
    }
};