import { playSound, playMusic, stopMusic } from "@/audio"

export const version = "0.4"

export const dureeJeu = 30 * 60 * 1000

export const state = {}

export const eatAndDrink = (onEmptyRessource) => {
    state.ressources.food -= getFoodConsumedPerDay();
    state.ressources.water -= getWaterConsumedPerDay();
    if (state.ressources.food < 0) {
        if (state.squads.length > 0) {
            state.squads.pop();
            newMessage({
                title: "Pnurie de nourriture",
                text: `Les stocks de nourriture sont vides. Une escouade a quitt la base pour en chercher, mais je ne crois pas qu'on les verra revenir.`
            })
        } else if (state.scientifics.length > 0) {
            state.scientifics.pop();
            newMessage({
                title: "On a faim !",
                text: `Les scientifiques sont affams  lcher les murs. Une quipe a dsert et tente sa chance dans la ville d' ct. Mais ils n'ont aucune chance de s'en sortir.`
            })
        } else {
            onEmptyRessource('food')
        }
    }
    else if (state.ressources.water < 0) {
        if (state.squads.length > 0) {
            state.squads.pop();
            newMessage({
                title: "Pnurie d'eau",
                text: `Les escouades rclament  boire ! La dernire citerne est vide, une quipe a dsert pour se diriger vers la rivire.`
            })
        } else if (state.scientifics.length > 0) {
            state.scientifics.pop();
            newMessage({
                title: "De l'eau !",
                text: `Les scientifiques sont dshydrats et ne parviennent plus  travailler. Une des quipes a quitt la base aprs que plusieurs d'entre eux aient perdu connaissance.`
            })
        } else {
            onEmptyRessource('water')
        }
    }

    if (state.ressources.food < 0) {
        state.ressources.food = 0;
    }
    if (state.ressources.water < 0) {
        state.ressources.water = 0;
    }

}

export const getFoodConsumedPerDay = () => state.squads.length + state.scientifics.length + state.foodPerDay
export const getWaterConsumedPerDay = () => state.squads.length + state.scientifics.length + state.waterPerDay

export const calcMissionChanceSucces = () => {
    return state.missionChanceSucces // technos comptent pour 75% du succs
        + Math.min(1, state.ressources.plutonium / reserve.plutonium) * 15
        + Math.min(1, state.ressources.water / reserve.water) * 5
        + Math.min(1, state.ressources.food / reserve.food) * 5
}

export const reserve = {
    food: 100,
    water: 100,
    //metal: 50,
    plutonium: 30
}

