<template>
    <div id="score">
        <p>Chances de succs du projet Odysse:</p>
        <h1>{{missionChanceSucces.toFixed(2)}} %</h1>

        <p>Rserves du vaisseau</p>
        <div class="bar food">
            <div class="barcompleted" :style="styleBar('food')"></div>
            {{ressources.food}} <div class="icon food"></div> / {{reserve.food}}
        </div>
        <div class="bar water">
            <div class="barcompleted" :style="styleBar('water')"></div>
            {{ressources.water}} <div class="icon water"></div> / {{reserve.water}}
        </div>
        <div class="bar plutonium">
            <div class="barcompleted" :style="styleBar('plutonium')"></div>
            {{ressources.plutonium}} <div class="icon plutonium"></div> / {{reserve.plutonium}}
        </div>

        <p>Technologies dveloppes</p>
        <h2>{{projectsDone.length}} / {{projects.length}}</h2>

        <div class="action disabled" v-if="!canTakeOff">
          <a>Dcollage impossible</a>
          <small>{{reasonWhyCantTakeOff}}</small>
        </div>
        <div class="action decollage" v-else>
          <div class="icon rocket"></div>
          <router-link to="/gameover">Dcollage du vaisseau</router-link>
          <small>Cette action est irrversible, assurons-nous d'tre prts</small>
        </div>
    </div>
</template>

<script>
import { state, reserve, calcMissionChanceSucces } from "@/game";
import { projects } from "@/projects";

export default {
  name: "Score",

  data() {
    return {
      missionChanceSucces: calcMissionChanceSucces(),
      ressources: state.ressources,
      projects: projects,
      projectsDone: state.projectsDone,
      reserve: reserve
    };
  },

  computed: {
    canTakeOff() {
      return (
        state.projectsDone.includes("Racteur nuclaire") &&
        state.ressources.plutonium >= 10 &&
        state.ressources.food >= 30 &&
        state.ressources.water >= 30
      );
    },

    reasonWhyCantTakeOff() {
      if (!state.projectsDone.includes("Racteur nuclaire"))
        return "Il nous faut installer le racteur nuclaire";
      if (state.ressources.plutonium < 10)
        return "On a besoin de 10 units de plutonium pour dcoller";
      if (state.ressources.food < 30)
        return "Pas question de partir sans 30 caisses de nourriture au moins";
      if (state.ressources.water < 30)
        return "Et l'eau ? Il nous faut au moins 30 citernes avant de dcoller";
    }
  },

  created() {
    this.interval = setInterval(() => {
      this.missionChanceSucces = calcMissionChanceSucces();
    }, 1000);
  },

  destroyed() {
    clearInterval(this.interval);
  },

  methods: {
    styleBar(res) {
      const colors = {
        food: "rgba(255,85,0,0.25)",
        water: "rgba(0,150,255,0.25)",
        plutonium: "rgba(85,255,0,0.25)"
      };

      let pc = Math.min(1, this.ressources[res] / this.reserve[res]) * 100;

      return {
        width: pc + "%",
        backgroundColor: colors[res]
      };
    }
  }
};
</script>

