  display: block;
  margin: 0.5em 0;
  position: relative;
}

.barcompleted {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
}

.icon {
  display: inline-block;
  width: 36px;
  height: 36px;
  background-size: 36px 36px;
  vertical-align: middle;
}

small {
  font-size: 0.7em;
}

